
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
a {
  position: relative;
  text-decoration: none;
  color: #3F51B5;
  cursor: pointer;
}
a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: #3F51B5;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}
a:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}
